<template>
  <xesLogin appId="mdfwxt" businessChannel="mdfwxt" :wxUnionid="wxUnionid" :wxAuthUrl="wxAuthUrl" :isSwitchAccount="isSwitchAccount" />
</template>

<script>
import xesLogin from '@xes/xes-business-lib/lib/login'
import '@xes/xes-business-lib/lib/style/login.css'
export default {
  components: {
    xesLogin
  },
  data () {
    const BASE_PATH = process.env.NODE_ENV === 'production' ? '/h5' : ''
    const redirectUrl = encodeURIComponent(
      `http://one-dev.speiyou.cn:8082${BASE_PATH}/login`
    )
    const wxAuthUrl = `https://open.weixin.qq.com/connect/oauth2/authorize?appid=wx4825ade40bfc434e&redirect_uri=${redirectUrl}&response_type=code&scope=snsapi_base&state=123#wechat_redirect`
    return {
      wxAuthUrl,
      isSwitchAccount: true,
      wxUnionid: ''
    }
  },
  mounted () {
    const { query } = this.$route
    if (query.code) {
      this.isSwitchAccount = true
      this.wxUnionid = query.code || ''
    }
  }
}
</script>
